
#app {
  position: relative;
  width: 100%;
  height: 100%;
  background: url(./static/img/AodaCat.6cd2fd5.jpg);
  background-size: 100%;
  opacity: .8;
}


.calculator{
    overflow: hidden;
    color: #ffffff;
    text-shadow: 0px 0px 5px #323233;
}
.bottom{
    overflow: hidden;
    position: absolute;
    bottom: 0
}
.data{
    float: right;
}
.operater{
    text-align: left;
    padding: 5px;
    float: left;
    margin-top: 16px;
}
.operator2{
    float: right;
    width: 20%;
    background-color: rgba(109, 172, 214, 0.5);
}
.operator2 li{
    text-align: center;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 30px;
    border-top: 1px solid #c1c1c1;
    border-right: 1px solid #c1c1c1;
    height: 80px;
}
.operator2 li:last-child{
    padding: 54px 0;
    border-bottom: 1px solid #c1c1c1;
    height: 160px;
}
.whiteboard{
    width: 80%;
    height: 60px;
    text-align: right;
    font-size: 24px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.num{
    width: 80%;
    float: left;
    overflow: hidden;
    border-bottom: 1px solid #c1c1c1;
    background-color: rgba(240, 247, 252, 0.5);
}
.operator{
    width: 80%;
    float: left;
    overflow: hidden;
    background-color: rgba(109, 172, 214, 0.5);
}
.num li,.operator li{
    float: left;
    width: 33.33%;
    text-align: center;
    font-size: 24px;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-left: 1px solid #c1c1c1;
    border-top: 1px solid #c1c1c1;
    height: 80px;
}
.operator li{
    font-size: 30px;
}
.num li:nth-child(3n),.operator li:nth-child(3n){
    border-right: 1px solid #c1c1c1;
}
li.on{
    background: rgba(246, 245, 245,.2)
}
.delete{
    background: url(data:image/png;base64,) no-repeat center center;
}
/**
 * Eric Meyer's Reset CSS v2.0 (http://meyerweb.com/eric/tools/css/reset/)
 * http://cssreset.com
 */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font-weight: normal;
    vertical-align: baseline;
    font-family: 'PingFang SC', 'helvetica neue', 'arial', 'hiragino sans gb', 'microsoft yahei ui', 'microsoft yahei', 'simsun', 'sans-serif';
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
    display: block
}

bodyf {
    line-height: 1;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

/* custom */

a {
    color: #7e8c8d;
    -webkit-backface-visibility: hidden;
    text-decoration: none;
}

li {
    list-style: none;
}

body {
    -webkit-text-size-adjust: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html,
body {
    height: 100%;
}

.toast_tip {
    padding: 12px 22px;
    color: #fff;
    background: #000;
    opacity: 0.7;
    position: fixed;
    left: 10%;
    top: 40%;
    font-size: 14px;
    border-radius: 8px;
    display: none;
    z-index: 10000;
    margin-left: -45px
}

@-webkit-keyframes myrotateY {
    0%,
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0)
    }
    12.5% {
        -webkit-transform: translateY(-16px);
                transform: translateY(-16px)
    }
    25% {
        -webkit-transform: translateY(-22px);
                transform: translateY(-22px)
    }
    37.5% {
        -webkit-transform: translateY(-16px);
                transform: translateY(-16px)
    }
    50% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px)
    }
    67.5% {
        -webkit-transform: translateY(32px);
                transform: translateY(32px)
    }
    75% {
        -webkit-transform: translateY(38px);
                transform: translateY(38px)
    }
    87.5% {
        -webkit-transform: translateY(34px);
                transform: translateY(34px)
    }
}

@keyframes myrotateY {
    0%,
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0)
    }
    12.5% {
        -webkit-transform: translateY(-16px);
                transform: translateY(-16px)
    }
    25% {
        -webkit-transform: translateY(-22px);
                transform: translateY(-22px)
    }
    37.5% {
        -webkit-transform: translateY(-16px);
                transform: translateY(-16px)
    }
    50% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px)
    }
    67.5% {
        -webkit-transform: translateY(32px);
                transform: translateY(32px)
    }
    75% {
        -webkit-transform: translateY(38px);
                transform: translateY(38px)
    }
    87.5% {
        -webkit-transform: translateY(34px);
                transform: translateY(34px)
    }
}

@-webkit-keyframes myrotateX {
